<!-- 仅为实例 -->
<template>
	<view :class="['outBtn', btnTypeCls, {'none-margin-right': noneMarginRight}]" 
	      @click="$emit('click')">
		<slot></slot>
	</view>
</template>

<script>

const TYPE_CLS_MAP = {
	'primary': { cls: 'mirrorego-button-primary' },
	'danger': { cls: 'mirrorego-button-danger' },
	'warning': { cls: 'mirrorego-button-warning' }
}

export default {
	name: 'mirroregoButton',
	props: {
		type: {
			type: String,
			default: 'primary'
		},
		noneMarginRight: {
			type: Boolean,
			default: false
		}
	},
	
	data () {
		return {};
	},
	
	computed: {
		btnTypeCls () {
			return TYPE_CLS_MAP[this.type].cls;
		}
	},
};
</script>

<style lang="scss">
.outBtn {
	display: inline-block;
	padding: 0 30rpx;
	height: 50rpx;
	line-height: 50rpx;
	font-size: 24rpx;
	border-radius: 10rpx;
	color: #fff;
	text-align: center;
	margin: 18rpx 18rpx 18rpx 0;
}
.none-margin-right {
	margin-right: 0;
}
.mirrorego-button {
	&-primary {
		background-color: #23a5fe;
	}
	&-danger {
		background-color: #ee0a24;
	}
	&-warning {
		background-color: #ff976a;
	}
}
</style>
